@import url(https://fonts.googleapis.com/css?family=Roboto+Mono:300,400,500,700&display=swap);
@import url(https://fonts.googleapis.com/icon?family=Material+Icons);
* {
  box-sizing: border-box;
  font-family: 'Roboto', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

*:focus {
  outline: none;
}

html, body {
  margin: 0;
  height: 100%;
  background-color: #151515 !important;
  font-size: 16px !important;
}

#root {
  min-height: 100%;
  height: 100%;
  max-height: 100%;
}

[draggable] {
  -webkit-user-select: none;
  -ms-user-select: none;
      user-select: none;
  -moz-user-drag: element;
  -webkit-user-drag: element;
  -khtml-user-drag: element;
  user-drag: element;
}


.outer-container {
  height: 100%;
  padding: 32px;
}

.inner-container {
  border: 1px solid #202020;
  height: 100%;
  overflow: auto;
  position: relative;
}

/* Hide scrollbar for Chrome, Safari and Opera */
.inner-container::-webkit-scrollbar {
  display: none;
}

/* Hide scrollbar for IE, Edge and Firefox */
.inner-container {
  -ms-overflow-style: none;  /* IE and Edge */
  scrollbar-width: none;  /* Firefox */
}

.image-container {
 position: absolute;
}

.hexgrid-container {
 position: absolute;
 top: 0px;
 z-index: 2;
}

.asset-container {
 position: absolute;
 top: 0px;
 z-index: 3;
}

.drag-box {
  cursor: move;
  position: absolute;
}

.title-container {
 position: relative;
 z-index: 1;
}

.fab-container {
  position: absolute;
  left: 64px;
  top: 64px;
  z-index: 1299;
}

.settings-fab * {
  font-size: 32px;
}

.showOnPrint {
  display: none;
}

@media print {

  * {
    color: black !important;
  }

  .hideOnPrint {
    display: none !important;
  }

  .showOnPrint {
    display: inline;
    display: initial;
  }

}


svg .hexagons {

}

svg .hexagons g {
  fill: rgba(0,0,0,0);
  fill-opacity: 0.6;
}

svg .hexagons g:hover {
  fill: rgba(255,255,255,0.15);
  fill-opacity: 0.0;
}

svg .hexagons g:hover text {
  fill-opacity: 1;
}

svg .hexagons g polygon {
  stroke: rgba(255,255,255,0.1);
  stroke-width: 0.2;
  transition: fill-opacity .5s;
}

svg .hexagons g polygon:hover {
  stroke: rgba(0,255,255,0.35);
  stroke-width: 0.2;
  transition: fill-opacity 1.5s;
}

svg .hexagons g text {
  font-size: 0.06em;
  fill: white;
  fill-opacity: 0.7;
  transition: fill-opacity .5s;
}
svg .hexagons path {
  fill: none;
  stroke: rgba(255,255,255,0.05);
  stroke-width: 0.2em;
  stroke-opacity: 0.7;
  stroke-linecap: round;
  stroke-linejoin: round;
}

svg .hexagons g.blocked {
    fill: #465880;
}
svg .hexagons g.blocked:hover {
    fill: #53699a;
}

